import VisualComponentDOM from "jx/comps/visualcomponent/VisualComponentDOM";
export default class SimpleButtonDOM extends VisualComponentDOM {
    constructor(dataObject) {
        super(Object.assign({ render: "DOM" }, dataObject));
    }
    _create() {
        super._create();
        this.view = $('<button type="button" id="" title=""></button>');
        this.view.prop("id", this.dataObject.id);
        this.view.prop("title", this.dataObject.id);
        this.view.text(this.dataObject.label || this.dataObject.id);
    }
    _initSync() {
        super._initSync();
        $(this.view).css("position", "absolute");
    }
}
